const media = {
    'xs': '@media (min-width: 320px)',
    'sm': '@media (min-width: 768px)',
    'md': '@media (min-width: 992px)',
    'lg': '@media (min-width: 1200px)'
};

const colors = {
    hoverLink: '#04A1E2',
    mainColor: '#7aafc3'
};

export {
    media,
    colors
}



